/* header */
@media only screen and (max-width: 1440px) {
  .header__personal::before {
    display: none; } }

@media only screen and (max-width: 1368px) {
  .header__navigation {
    display: none; }
  .header__burger-line,
  .header__burger-line::after,
  .header__burger-line::before {
    background-color: #c7c5c4;
    content: "";
    cursor: pointer;
    width: 35px;
    /*35px*/
    height: 2px;
    /*2px*/
    position: relative;
    display: block;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease; }
  .header__burger-line::after,
  .header__burger-line::before {
    position: absolute;
    right: 0; }
  .header__burger-line::after {
    bottom: 10px;
    /* 10px */
    width: 40px;
    /* 40px */ }
  .header__burger-line::before {
    top: 10px;
    /* 10px */
    width: 30px;
    /* 30px */ }
  ._active {
    position: fixed;
    /* overflow-y: hidden; */
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    z-index: 1;
    background-color: #12c37c;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center; }
  div.header__navigation._active {
    left: 0;
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out; }
  ._active + .header__burger-btn .header__burger-line::after {
    transform: rotate(90deg) translate(10px, 0);
    -webkit-transform: rotate(90deg) translate(10px, 0);
    -moz-transform: rotate(90deg) translate(10px, 0);
    -ms-transform: rotate(90deg) translate(10px, 0);
    -o-transform: rotate(90deg) translate(10px, 0); }
  ._active + .header__burger-btn .header__burger-line {
    width: 40px;
    transform: rotate(-45deg) translate(-15px, 0);
    -webkit-transform: rotate(-45deg) translate(-15px, 0);
    -moz-transform: rotate(-45deg) translate(-15px, 0);
    -ms-transform: rotate(-45deg) translate(-15px, 0);
    -o-transform: rotate(-45deg) translate(-15px, 0); }
  ._active + .header__burger-btn .header__burger-line::before {
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0); }
  .menu__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 800px; }
  .menu__item + .menu__item {
    margin-left: 0; }
  .menu__link {
    font-size: 1.25rem; }
  .header__right-block {
    margin: 160px 0 240px; }
  .header__logo {
